@import "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
html {
  font-size: 62.5%;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Georgia, "Times New Roman", Times, serif;
}

body {
  font-weight: normal;
  background-color: navy;
  color: beige;
  height: 100vh;
  width: 100vw;
}

.app {
  display: grid;
  justify-items: center;
  height: 100vh;
  width: 100vw;
}

.navigation-wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-content: space-evenly;
  width: 100vw;
  margin: 0px;
  height: fit-content;
}
.navigation-wrapper .navbar {
  width: 100vw;
}
.navigation-wrapper .container-fluid {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigation-wrapper .container-fluid .img-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navigation-wrapper .container-fluid .img-wrapper img {
  border-radius: 35px;
  width: 150px;
}
.navigation-wrapper .container-fluid .navbar-noimg {
  display: flex;
  justify-content: center;
  align-items: center;
  word-wrap: normal;
}
.navigation-wrapper .container-fluid .navbar-noimg p {
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 2.7rem;
  padding: 2px;
}
.navigation-wrapper .container-fluid .links {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
}
.navigation-wrapper .container-fluid .links Nav {
  flex-wrap: inherit;
}
.navigation-wrapper .container-fluid .links .navbarLinks {
  color: beige;
  background-color: #36454f;
  margin: 0px 5px;
  padding: 3px 6px;
  font-size: 2rem;
}

@media screen and (min-width: 0px) and (max-width: 576px) {
  .navigation-wrapper {
    grid-template-columns: 1fr;
    height: fit-content;
  }
  .navigation-wrapper .navbar {
    height: fit-content;
  }
  .navigation-wrapper .container-fluid {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navigation-wrapper .container-fluid .links .navbarLinks {
    font-size: 1.25rem;
  }
}
@media screen and (min-width: 576px) and (max-width: 900px) {
  .navigation-wrapper {
    grid-template-columns: 1fr;
    height: fit-content;
  }
  .navigation-wrapper .navbar {
    height: fit-content;
  }
  .navigation-wrapper .container-fluid {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.about-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-radius: 5px;
  background-color: slategray;
  color: navy;
  padding: 5px;
}
.about-wrapper .leftside-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
.about-wrapper .rightside-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 5px;
  align-items: center;
}
.about-wrapper .rightside-wrapper .title-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 25px;
  padding: 0;
}
.about-wrapper .rightside-wrapper .title-wrapper h2 {
  font-size: 3rem;
  font-weight: 700;
}
.about-wrapper .rightside-wrapper .info-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 25px;
  margin: 0;
}
.about-wrapper .rightside-wrapper .info-wrapper .about-us-text {
  font-size: 2.5rem;
  letter-spacing: 1px;
  text-align: center;
}

@media (min-width: 0px) and (max-width: 576px) {
  .about-wrapper {
    grid-template-columns: 1fr;
  }
  .about-wrapper .leftside-wrapper {
    height: 250px;
  }
  .about-wrapper .rightside-wrapper .title-wrapper h2 {
    font-size: 2rem;
    font-weight: 700;
  }
  .about-wrapper .rightside-wrapper .info-wrapper .about-us-text {
    font-size: 1.75rem;
  }
}
.home-page-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3px;
}
.home-page-wrapper .title-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 3rem;
  margin-top: 4px;
}
.home-page-wrapper .title-wrapper .select-options label {
  font-size: 2rem;
  margin-right: 10px;
}
.home-page-wrapper .title-wrapper .select-options select {
  text-align: center;
  font-size: 2rem;
  width: 150px;
}
.home-page-wrapper .title-wrapper .select-options select option {
  font-size: 1.5rem;
}
.home-page-wrapper .column-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  margin-top: 15px;
  width: 99vw;
}
.home-page-wrapper .column-wrapper .right-column {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  width: 100vw;
}

@media screen and (min-width: 0px) and (max-width: 454px) {
  .home-page-wrapper .column-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 5px;
    gap: 10px;
  }
  .home-page-wrapper .column-wrapper .right-column {
    display: grid;
    grid-template-columns: 1fr;
  }
}
@media screen and (min-width: 455px) and (max-width: 679px) {
  .home-page-wrapper .column-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 5px;
    gap: 10px;
  }
  .home-page-wrapper .column-wrapper .right-column {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (min-width: 680px) and (max-width: 1149px) {
  .home-page-wrapper .column-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 5px;
    gap: 10px;
  }
  .home-page-wrapper .column-wrapper .right-column {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.contact-wrapper {
